import {addData} from '../../server/employee'
export default {
    name:'addEmployee',
    data(){
        return{
            employeeName:'',//姓名
            value:'',
            customerEmployeeNo:'',//工号
            organizationalId:'',//所属业务区
            entryAt:'',//日期
            customerId:'',
            positionType:'',
            legalCompanyId:''
        }
    },
    methods:{
        openPicker() {
        this.$refs.picker.open();
      },
      handleConfirm(value){
          console.log(value);
      },
        handleService(){
            this.$router.push({
                path:'/SelectService'
            })
        },
        handleClient(){
            this.$router.push({
                path:'/ClientType'
            })
        },
        openlabour(){
            this.$refs.picker.open();
        },
        handleType(){
            console.log(1)
        },
        getAddData(){
            addData({
                "employeeName":this.employeeName,
                "customerEmployeeNo":this.customerEmployeeNo,
                "organizationalId":this.organizationalId,//所属业务id
                "entryAt":this.entryAt,//"2019-12-20 00:00:00",
                "customerId":this.customerId,//150,
                "positionType":this.positionType,//"POST_STATION_02",
                "legalCompanyId":this.legalCompanyId//5
            }).then(e=>{
                console.log(e);
                if(e.data.code==200){
                    console.log(e);
                }
            })
        }
    }
}



